
.Pokecard-title {
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 1.5rem;
}

.Pokecard {
  color: #4d4d4d;
  width: 300px;
  padding: 10px;
  margin: 1rem;
  -webkit-box-shadow: 7px 6px 7px -1px rgba(0,0,0,0.15);
-moz-box-shadow: 7px 6px 7px -1px rgba(0,0,0,0.15);
box-shadow: 7px 6px 7px -1px rgba(0,0,0,0.15);
text-align: center;
background-color: white;
border-radius: 3px;
}

.Pokecard-data {
  font-size: 80%;
}

.Pokecard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 1rem;
}

.Pokecard img {
  transition: all ease 500ms;
}

.Pokecard img:hover {
  transform: scale(1.25);
  opacity: 0.7;
}